.modal {
    width: 90%;
    max-width: 500px;
    margin-left: auto;
    margin-right: auto;
    margin-top: 10px;
    margin-bottom: 10px;
    background-color: #fff;
    border-radius: 0.5rem;
    box-shadow: 0 5px 15px rgba(0, 0, 0, 0.2);
  }
  
  .modal-header {
    display: flex;
    align-items: flex-start;
    justify-content: space-between;
    padding: 1.5rem 1.5rem 1rem;
  }
  
  .logo-circle {
    width: 3.5rem;
    height: 3.5rem;
    display: flex;
    justify-content: center;
    align-items: center;
    border-radius: 50%;
    background-color: #e9e5ff;
    fill: rgb(97, 18, 136);
  }
  
  .btn-close {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 2.25rem;
    height: 2.25rem;
    border-radius: 0.25rem;
    border: none;
    background-color: transparent;
    cursor: pointer;
  }
  
  .btn-close:hover,
  .btn-close:focus {
    background-color: #e9e5ff;
  }
  
  .modal-body {
    padding: 1rem 1.5rem;
    display: flex;
    flex-direction: column;
    align-items: center
  }
  
  .modal-title {
    font-family: "Mochiy Pop One", sans-serif;
    font-weight: 400;
    font-size: 18px;
  }
  
  .modal-description {
    color: #6a6b76;
  }
  
  .upload-area {
    margin-top: 1.25rem;
    background-color: transparent;
    padding: 3rem;
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    border: 1px dashed #6a6b76;
  }
  
  .upload-area:hover,
  .upload-area:focus {
    cursor: pointer;
    border: 1px solid #6a6b76;
  }
  
  .upload-area:hover .upload-area-icon,
  .upload-area:focus .upload-area-icon {
    transform: scale(1.3);
    transition-duration: 0.3s;
  }
  
  .upload-area-icon {
    display: block;
    width: 2.25rem;
    height: 2.25rem;
    fill: rgb(97, 18, 136);
  }
  
  .upload-area-title {
    margin-top: 1rem;
    display: block;
    font-weight: 700;
    color: #0d0f21;
  }
  
  .upload-area-description {
    display: block;
    color: #6a6b76;
  }
  
  .upload-area-description strong {
    color: rgb(97, 18, 136);
    font-weight: 700;
  }
  
  .modal-footer {
    padding: 1rem 1.5rem 1.5rem;
    display: flex;
    justify-content: flex-end;
  }
  
  .modal-footer [class*="btn-"] {
    margin-left: 0.75rem;
  }
  
  .btn-secondary,
  .btn-primary {
    padding: 0.5rem 1rem;
    font-weight: 500;
    border: 2px solid #e5e5e5;
    border-radius: 0.25rem;
    background-color: transparent;
    cursor: pointer;
  }
  
  .btn-primary {
    color: #fff;
    background-color: rgb(97, 18, 136);
    border-color: rgb(97, 18, 136)2;
    cursor: pointer;
  }


.modal-body img{
    width: 150px;
    height: auto;
    
}